import React from 'react';
import styles from './NavigationItems.module.scss';
import NavigationItem from './NavigationItem/NavigationItem';

const navigationItems = (props) => {
    return(
    <ul className={styles.NavigationItems}>
    <NavigationItem link='/ui' >UI/UX</NavigationItem>
    <NavigationItem link='/brandig'>Brandig</NavigationItem>
    <NavigationItem link='/Print'>Print</NavigationItem>
    <NavigationItem link='/3D Desing'>3D Desing</NavigationItem>
    <NavigationItem link='/Book Desing'>Book Desing</NavigationItem>
    <NavigationItem link='/Product Desing'>Product Desing</NavigationItem>
    </ul>
    );
}

export default navigationItems;